<template>
  <div
    v-if="forms !== undefined && categories !== undefined"
    id="responses"
    class="box"
    style="overflow-x: auto;"
  >
    <table class="table is-striped is-hoverable is-fullwidth">
      <caption class="title is-scrollable">
        Formulários da Plataforma
      </caption>
      <thead>
        <tr>
          <th>
            Formulário
          </th>
          <th
            align="center"
            colspan="2"
          >
            Respostas
          </th>
          <th align="center">
            Tipo
          </th>
          <th align="center">
            Categoria
          </th>
          <th align="center">
            Ativo
          </th>
          <th />
          <th />
          <th />
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="form in forms"
          :key="form.id"
        >
          <td>
            {{ form.title }}
          </td>
          <td
            align="right"
            class="subtitle"
          >
            <a
              v-if="form.responses > 0"
              class=" flex row vca ra
              "
              :href="'responses/'+form.id"
            >
              {{ form.responses }}
            </a>
            <span v-else>
              {{ form.responses }}
            </span>
          </td>
          <td align="left">
            <a
              v-if="form.responses > 0"
              :href="'responses/'+form.id"
            >
              <span
                class="icon flex row vca"
                style="font-size: 1.2em;"
              >
                <i class="fas fa-eye" />
              </span>
            </a>
          </td>
          <td align="right">
            <div class="select is-rounded is-fullwidth">
              <select
                v-model.number="form.type"
                style="color : black;"
                :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
                @change="changeType(form.id, form.type == '' ? null : form.type)"
              >
                <option
                  v-for="(type, key) in types"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ type.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="right">
            <div class="select is-rounded is-fullwidth">
              <select
                v-model.number="form.category"
                style="color : black;"
                :disabled="form.category && (categories[form.category].formsignupid == form.id || categories[form.category].formproposalid == form.id || categories[form.category].formfeedbackid == form.id)"
                @change="changeCategory(form.id, form.category == '' ? null : form.category)"
              >
                <option />
                <option
                  v-for="(category, key) in categories"
                  :key="key"
                  :value="key"
                  style="background-color: white; color: black;"
                >
                  {{ category.name }}
                </option>
              </select>
            </div>
          </td>
          <td align="center">
            <label
              class="switch"
              style="margin-top: 7px;"
            >
              <input
                v-if="form.type==1"
                type="checkbox"
                :checked="form.category && categories[form.category].formsignupid == form.id"
                :disabled="form.category && categories[form.category].formsignupid == form.id"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <input
                v-if="form.type==2"
                type="checkbox"
                :checked="form.category && categories[form.category].formproposalid == form.id"
                :disabled="form.category && categories[form.category].formproposalid == form.id"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <input
                v-if="form.type==3"
                :checked="form.category && categories[form.category].formfeedbackid == form.id"
                :disabled="form.category && categories[form.category].formfeedbackid == form.id"
                type="checkbox"
                @change="changeActive(form.category, form.id, form.type)"
              >
              <span class="slider round noafter" />
            </label>
          </td>
          <td align="center">
            <a :href="'duplicateForm/' + form.id">
              <i class="far fa-clone" />
            </a>
          </td>
          <td align="center">
            <a 
              :href="'editForm/' + form.id" 
            >
              <i class="fas fa-edit" />
            </a>
          </td>
          <td align="center">
            <a
              v-if="form.responses == 0"
              href="#delete"
              @click="remove(form.id)"
            >
              <i class="fas fa-trash-alt" />
            </a>           
          </td>
        </tr>
        <tr>
          <td
            colspan="9"
            align="center"
            class="has-background-info-light"
          >
            <a
              class="flex row ca width-100"
              href="createForm"
            >Criar novo formulário</a>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
export default {
  name: "ViewEvents"
};
</script>

<style scoped lang="scss">
  .noafter:after {
    content: '' !important; 
  }

  th, td, a {
    vertical-align: middle !important;
  }
</style>
